import {API} from '../../services/services.api'

const ON_URL_CHANGE = 'ON_URL_CHANGE'
const ON_ANSWER_CHANGE = 'ON_ANSWER_CHANGE'
const ADD_SURVEY_FORM = 'ADD_SURVEY_FORM'
const initialState = {
    surveys: {
        s1: {
            s_url: '',
            s_right_answer: ''
        }
    },
    winners: []
}

const GET_POLL_BY_ID = 'GET_POLL_BY_ID'

export default (state = initialState, action) => {
    const {type, payload} = action
    switch (type) {
        case ON_ANSWER_CHANGE: {
            const {s_key, value} = payload
            return {
                ...state,
                surveys: {
                    ...state.surveys,
                    [s_key]: {
                        ...state.surveys[s_key],
                        s_right_answer: value
                    }
                }
            }
        }
        case ON_URL_CHANGE: {
            const {s_key, value} = payload
            return {
                ...state,
                surveys: {
                    ...state.surveys,
                    [s_key]: {
                        ...state.surveys[s_key],
                        s_url: value
                    }
                }
            }
        }
        case ADD_SURVEY_FORM: {
            let surveysLength = Object.keys(state).length
            surveysLength === 0 ? (surveysLength += 2) : (surveysLength += 1)
            const surveyID = 's' + surveysLength
            return {
                ...state,
                surveys: {
                    ...state.surveys,
                    [surveyID]: {s_url: '', s_right_answer: ''}
                }
            }
        }
        case GET_POLL_BY_ID: {
            const pollData = payload
        }
        default:
            return state
    }
}

export const GetPollById = (ownerID, pollID, s_key) => {
    ownerID = parseInt(ownerID)
    pollID = parseInt(pollID)

    console.log(typeof ownerID, typeof pollID);

    API.getPollById(ownerID, pollID)
        .then((pollData) => {
            console.log(`${s_key} DATA`, pollData)
        })
        .catch((err) => {
            API.onSettingChange().then((r) => {
                API.getPollById(ownerID, pollID).then((pollData) => {
                    console.log(`${s_key} DATA`, pollData)
                })
            })
            API.getAccessToUserWall()
        })
}

export const onUrlChange = (e, s_key) => {
    return (dispatch) => {
        const value = e.target.value
        dispatch({
            type: ON_URL_CHANGE,
            payload: {value, s_key}
        })
    }
}

export const onAnswerChange = (e, s_key) => {
    return (dispatch) => {
        const value = e.target.value
        dispatch({
            type: ON_ANSWER_CHANGE,
            payload: {value, s_key}
        })
    }
}

export const addSurveyForm = () => {
    return (dispatch) => {
        dispatch({
            type: ADD_SURVEY_FORM
        })
    }
}

function getSurveyID(s_url) {
    const ownerAndPollID = s_url.split('poll-')[1]
    return ownerAndPollID.split('_')[1]
}

function getSurveyOwnerID(s_url) {
    const ownerAndPollID = s_url.split('poll-')[1]
    return ownerAndPollID.split('_')[0]
}

export const GetWinners = (surveys) => {
    return (dispatch) => {
        console.log('SURVEYS:', surveys)
        Object.keys(surveys).forEach((s_key) => {
            console.log('KEYS:', s_key)
            console.log('VALUES:', surveys[s_key])
            const surveyData = surveys[s_key]
            const surveyID = getSurveyID(surveyData.s_url)
            const ownerID = getSurveyOwnerID(surveyData.s_url)
            console.log('POLL_ID, OWNER_ID', surveyID, ownerID)
            GetPollById(ownerID, surveyID, s_key)
        })
    }
}
